<template>
  <v-toolbar
    app
    height='100'
    color='white'
  >
    <v-toolbar-side-icon
      class="hidden-md-and-up"
      @click="toggleDrawer"
    />
    <v-container
      mx-auto
      py-0
    >
      <v-layout>
        <v-img
          :src="require('@/assets/logo.png')"
          class="mr-5"
          contain
          height="100"
          width="150"
          max-width="150"
          @click="$vuetify.goTo(0)"
        />
        <v-btn
          class="ml-0 hidden-sm-and-down"
          style='margin-top: 30px;'
          flat
          href='https://www.mercato.com/shop/natural-frontier-market-long-island-city'
        >
          Order Online
        </v-btn>
        <v-btn
          class="ml-0 hidden-sm-and-down"
          style='margin-top: 30px;'
          flat
          href='./june-deals.PDF'
        >
          Monthly Deals
        </v-btn>
      </v-layout>
    </v-container>
  </v-toolbar>
</template>

<script>
  // Utilities
  import {
    mapGetters,
    mapMutations
  } from 'vuex'

  export default {
    computed: {
      ...mapGetters(['links'])
    },

    methods: {
      ...mapMutations(['toggleDrawer']),
      onClick (e, item) {
        e.stopPropagation()

        if (item.to || !item.href) return

        this.$vuetify.goTo(item.href)
      }
    }
  }
</script>
